.mouse {
  width: 25px;
  height: 40px;
  border: 2px solid #333;
  border-radius: 60px;
  position: relative;
  overflow: hidden
}

.mouse::before {
  content: '';
  width: 5px;
  height: 5px;
  position: absolute;
  top: 7px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #333;
  border-radius: 50%;
  opacity: 1;
  animation: wheel 1.3s infinite;
  -webkit-animation: wheel 1.3s infinite
}

@keyframes wheel {
  to {
      opacity: 0;
      top: 27px
  }
}

@-webkit-keyframes wheel {
  to {
      opacity: 0;
      top: 27px
  }
}
/* Home Hero Section */
.home-hero {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #000000;
  background-color: #333; /* Replace with your preferred color */
}

.home-hero__content {
  max-width: 800px;
  margin: 0 auto;
}

.heading-primary {
  font-size: 6rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  margin-bottom: 2rem;
}

/* Tagline Animation */
.home-hero__tagline {
  font-size: 2.5rem; /* Slightly bigger size */
  margin-bottom: 3rem;
  overflow: hidden; /* Hide overflow for animation */
  height: 3rem; /* Adjust based on font size */
}

.home-hero__tagline span {
  display: inline-block;
  opacity: 0;
  animation: slideIn 12s infinite; /* Animation duration */
}

.home-hero__tagline span:nth-child(1) {
  animation-delay: 0s;
}

.home-hero__tagline span:nth-child(2) {
  animation-delay: 4s;
}

.home-hero__tagline span:nth-child(3) {
  animation-delay: 8s;
}

@keyframes slideIn {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  10% {
    opacity: 1;
    transform: translateY(0);
  }
  30% {
    opacity: 1;
    transform: translateY(0);
  }
  40% {
    opacity: 0;
    transform: translateY(-100%);
  }
  100% {
    opacity: 0;
    transform: translateY(-100%);
  }
}

